.mobile-modal {
    display: none;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, .7);
    z-index: 15;
    overflow-y: auto;
    overflow-x: hidden;
}
.menu-mobile{
    position: relative;
    width: 315px;
    &>span {
        position: absolute;
        right: 0;
        color: var(--White);
        top: 10px;
        cursor: pointer;
    }
    &__inner{
        background-color: #ffffff;
        width: 290px;
    }
    &.fancybox-content{
        padding: 0;
        vertical-align: top;
        overflow-x: hidden;
        background: transparent;
    }
    .fancybox-button.fancybox-close-small{
        right: -5px;
        top: 3px;
        color: var(--White);
    }
    .popup-desc-parent span:first-of-type {
        display: inline-block;
    }
    .logIn span {
        display: inline-block;
    }
    .list-line{
        flex-direction: column;
        padding: 0 15px;
        li{
            border-left: none;
            border-bottom: 1px solid var(--Whisper);
            padding: 20px 0;
            &:first-of-type{
                justify-content: space-between;
            }
        }
    }
    &__list{
        width: 100%;
        li, ul {
            width: 100%;
        }
        li {
            flex-grow: 1;
        }
        a {
            flex-shrink: 1;
        }
        .item__inner{
            padding: 10px 15px 10px 22px;
            display: flex;
            align-items: center;
        }
        .firstStage{
            &> li{
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                &:hover{
                    &> .item__inner{
                        &, &> a{
                        color: var(--Flamenco);
                        &::after{
                            transform: rotate(180deg);
                        }
                    }
                    }
                }
            }
            &:first-of-type{
                &> .item__inner{
                    padding-top: 20px;
                    padding-bottom: 20px;
                }
            }
            &> .item__inner{
                &::after{
                    color: var(--mainColor);
                }
            }
            i{
                font-size: 17px;
                margin-right: 12px;
                &.icon-1-3_menu{
                    font-size: 12px;
                }
            }
            &> a{
                font-size: 17px;
                font-weight: 600;
            }
            li:hover{
                .secondStage{
                    display: block;
                    width: 100%;
                }
            }
        }
        .secondStage{
            width: 100%;
            border-bottom: 1px solid var(--Whisper);
            padding-bottom: 12px;
            overflow: hidden;
            display: none;
            .arrowDown{
                &::after{
                    transform: rotate(-90deg);
                    color: var(--PinkSwan);
                }
                &:hover{
                    &::after{
                        transform: rotate(0);
                    }
                }
            }
            &> li{
                &:hover{
                    &> .item__inner{
                        background-color: var(--Wedgewood);
                        a{
                            color: var(--White);
                        }
                    }
                }
                &> .item__inner{
                    &::after{
                        right: 12px;
                    }
                }
            }
            li:hover{
                .thirdStage{
                    display: block;
                    width: 100%;
                }
            }
        }
        .thirdStage{
            box-shadow: 0px 8px 15px 0px rgba(0, 0, 0, .15);
            display: none;
            li{
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                .item__inner{
                    width: 100%;
                    padding-left: 36px;
                    &:after {
                        right: 13px;
                    }
                }
                ul{
                    .item__inner{
                        padding-left: 46px;
                    }
                }
                &:hover{
                    .item__inner::after{
                        transform: rotate(180deg);
                    }
                    .fourth{
                        display: block;
                        width: 100%;
                    }
                }
            }
        }
        .fourth{
            display: none;
        }
    }
    .brends-list {
        width: 100%;
        li{
            border-bottom: 1px dashed #e1e1e1;
            padding-bottom: 5px;
            &:not(:last-of-type){
                margin-bottom: 16px;
            }
        }
        a{
            margin: 0 3px 10px 2px;
            display: inline-block;
            &:hover{
                color: var(--Flamenco)!important;
            }
        }
    }
}